.app__header {
  flex: 1;
  width: 100%;
  height: 100%;
  flex-direction: row;

  padding: 6rem 2rem 0;

  @media screen and (min-width:2000px) {
    padding-top: 8rem;
  }

  @media screen and (max-width:1200px) {
    flex-direction: column;
  }

  @media screen and (max-width:400px) {
    padding: 6rem 1rem 2rem;
  }

}

.app__header-intro {
  flex: 1;
  width: 100%;
  flex-direction: row;
  padding: 4rem 2rem;

  p {
    font-size: 20px;
  }

  .site-title h3 {
    padding-top: 5%;
    font-weight: 500;
  }


  #subheading {
    color: var(--secondary-color);
  }

  .title-text {
    font: normal bold 45px/ 72px var(--font-base);
  }

  .text-uppercase {
    text-transform: uppercase;
  }

  .first-btn a {
    text-decoration: none;
    color: #fff;
  }

  .first-btn a:hover {
    text-decoration: none;
    color: #000;
  }

  .second-btn a {
    text-decoration: none;
    color: #000;
  }

  .second-btn a:hover {
    text-decoration: none;
    color: #fff;
  }

}


/* Buttons */
.app__header-buttons {
  .button {
    padding: .8rem 2.2rem;
    font: normal 500 16px/20px var(--roboto);
    position: relative;
    border: 3px solid transparent;
    border-radius: 4px;
  }

  .button.primary-button {
    background: var(--gradient-color);
    background-clip: padding-box;
    color: whitesmoke;
    transition: background .6s ease;
    box-shadow: var(--box-shadow);
  }

  .button.primary-button:hover {
    background: whitesmoke;
    background-clip: padding-box;
    color: black;
  }

  .button.primary-button::after,
  .button.secondary-button::after {
    position: absolute;
    top: -2px;
    left: -2px;
    bottom: -2px;
    right: -2px;
    background: var(--gradient-color);
    content: ' ';
    z-index: -1;
    border-radius: 4px;
  }

  .button.secondary-button {
    background: white;
    background-clip: padding-box;
    transition: background .6s ease;
  }

  .button.secondary-button:hover {
    background: var(--gradient-color);
    color: whitesmoke;
    box-shadow: var(--box-shadow);
  }

  .first-btn a {
    text-decoration: none;
    color: #fff;
  }

  .first-btn a:hover {
    text-decoration: none;
    color: #000;
  }

  .second-btn a {
    text-decoration: none;
    color: #000;
  }

  .second-btn a:hover {
    text-decoration: none;
    color: #fff;
  }

  .site-title p a {
    text-decoration: underline;
    cursor: pointer;
  }

  .button.secondary-button:hover a {
    color: whitesmoke;
  }

  .button.primary-button:hover a {
    color: black;
  }
}



#home {
  position: relative;
  background: url('../../assets//bgIMG.png');
  background-size: cover;
  background-repeat: repeat;
  background-position: center;

  .app__wrapper {
    padding: 0;

    .copyright {
      display: none
    }
  }
}




